
.menu a{
    color:#000;
    list-style:none;
    display:inline;
    padding:20px 20px;
    font-size:1.1em;
    line-height:60px;
    font-family: 'Roboto', sans-serif;
    cursor:pointer;
}

.loader {
position: fixed;
top: 50%;
margin-top:30px;
left: 50%;
-webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
           border-style: solid;
           border-radius: 50%;
           border-color: #e65959;
-webkit-animation: 1s pulse infinite;
        animation: 1s pulse infinite;
}
.loadtext {
position: fixed;
top: 50%;
margin-top:60px;
left: 47%;
}
@-webkit-keyframes pulse {
0% {
height: 0px;
width: 0px;
       border-width: 32px;
opacity: 0;
}
40% {
opacity: 1;
}
80% {
height: 64px;
width: 64px;
       border-width: 0px;
opacity: 0;
}
100%{
opacity: 0;
}
}
@keyframes pulse {
0% {
height: 0px;
width: 0px;
       border-width: 32px;
opacity: 0;
}
40% {
opacity: 1;
}
80% {
height: 64px;
width: 64px;
       border-width: 0px;
opacity: 0;
}
100%{
opacity: 0;
}
}
